[class*='mu-icon-'],
[class^='mu-icon-'] {
  font-weight: 400;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  vertical-align: baseline;
  display: inline-block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
button {
  margin: 0;
  cursor: pointer;
  color: inherit;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
}
.mu-button {
  user-select: none;
  outline: none;
  white-space: nowrap;
  display: inline-block;
  line-height: 1;
  cursor: pointer;
  text-align: center;
  box-sizing: border-box;
  margin: 0;
  transition: all 0.3s;
  font-weight: 400;
  padding: 8px 15px;
  font-size: 14px;
  color: #666666;
  border: 1px solid #d9d9d9;
  background-color: #fff;
  border-radius: 4px;
}
.mu-button.shape-straight {
  border-radius: 0px;
}
.mu-button.shape-circle {
  border-radius: 32px;
}
.mu-button-mini {
  padding: 4px 5px;
}
.mu-button-mini.shape-circle {
  border-radius: 32px;
}
.mu-button-large {
  padding: 12px 19px;
}
.mu-button-large.shape-circle {
  border-radius: 32px;
}
.mu-button-block {
  width: 100%;
}
.mu-button-default {
  color: #666;
}
.mu-button-default:hover {
  color: #1bd2e2;
  border-color: #1bd2e2;
}
.mu-button-default:active {
  color: #00b6c7;
  border-color: #00b6c7;
}
.mu-button-primary {
  border-color: #00c7db;
  background-color: #00c7db;
  color: #fff;
}
.mu-button-primary:hover {
  border-color: #1bd2e2;
  background-color: #1bd2e2;
}
.mu-button-primary:active {
  border-color: #00b6c7;
  background-color: #00b6c7;
}
.mu-button-dashed {
  border: 1px dashed #d9d9d9;
}
.mu-button-dashed:hover {
  color: #1bd2e2;
  border-color: #1bd2e2;
}
.mu-button-dashed:active {
  color: #00b6c7;
  border-color: #00b6c7;
}
.mu-button-danger {
  border-color: #ff4d4f;
  background-color: #ff4d4f;
  color: #fff;
}
.mu-button-danger:hover {
  border-color: #ff7875;
  background-color: #ff7875;
}
.mu-button-danger:active {
  border-color: #d9363e;
  background-color: #d9363e;
}
.mu-button-text {
  border-color: transparent;
  background-color: transparent;
  color: #00c7db;
}
.mu-button-text:hover {
  color: #1bd2e2;
}
.mu-button-ghost {
  color: #fff;
  border-color: #fff;
  background: transparent;
}
.mu-button-ghost.mu-button-primary {
  color: #00c7db;
  border-color: #00c7db;
}
.mu-button-ghost.mu-button-primary:hover {
  color: #1bd2e2;
  background: transparent;
}
.mu-button-ghost.mu-button-primary:active {
  color: #00b6c7;
  border-color: #00b6c7;
}
.mu-button-ghost.mu-button-danger {
  color: #ff4d4f;
  border-color: #ff4d4f;
}
.mu-button-ghost.mu-button-danger:hover {
  color: #ff7875;
  background: transparent;
}
.mu-button-ghost.mu-button-danger:active {
  color: #d9363e;
  border-color: #d9363e;
}
.mu-button-ghost.mu-button-text {
  color: #fff;
  border: transparent;
}
.mu-button-ghost.mu-button-text:hover {
  color: #1bd2e2;
}
.mu-button-ghost.mu-button-text:active {
  color: #00b6c7;
}
.mu-button-disabled {
  cursor: not-allowed;
  border-color: #d9d9d9;
  color: #d3d3d3;
  background: #f5f5f5;
}
.mu-button-disabled:hover {
  color: #d3d3d3;
  border-color: #d9d9d9;
  background: #f5f5f5;
}
.mu-button-disabled:active {
  color: #d3d3d3;
  border-color: #d9d9d9;
  background: #f5f5f5;
}
.mu-button-disabled.mu-button-primary {
  color: #fff;
}
.mu-button-disabled.mu-button-text {
  color: #d3d3d3;
  border-color: transparent;
  background: transparent;
}
.mu-button-disabled.mu-button-ghost {
  border-color: #d9d9d9 !important;
  background: transparent;
}
.mu-button-loading {
  opacity: 0.5;
}
.mu-button-loading:hover {
  border-color: #00c7db;
  background-color: #00c7db;
}
.mu-button-loading:active {
  border-color: #00c7db;
  background-color: #00c7db;
}
.mu-icon-loading {
  margin-right: 4px;
  animation: rotating 2s linear infinite;
}
@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
